.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.movies_movie {
    width: 45%;
    margin-bottom: 70px;
    background-color: white;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}

.movies_movie img{
    position: relative;
    top: -50px;
    max-width: 150px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50,50,93,0.25), 0 18px 36px -18px rgba(0,0,0,0.3), 0 -12px 36px -8px rgba(0,0,0,0.025);
}

.movie_data h3,
.movie_data h5{
    margin: 0;
    font-weight: 300;
}
.movie_title{
    margin-bottom: 5px;
    font-size: 24px;
}

.movie_year,
.movie_genres,
.movie_summary{
    color: #b2b3bc;
}

.movie_genres{
    list-style: none;
    padding: 0;
    margin: 5;
    display: flex;
}

.movie_genres li{
    margin-left: 10px;
    font-size: 14px 0px;
}